        .p-4(style="background-color:#98E2EC;border-radius:9px;")
          p.mb-0 Antes de embarcarnos en la exploracin de los principios y tcnicas del dibujo tcnico asociado a las labores agrcolas, pecuarias o agroindustriales, es importante realizar una adecuada contextualizacin y una identificacin de los conocimientos necesarios para el aprendizaje. En esta seccin inicial de nuestra gua, estableceremos el escenario y sentaremos las bases para un aprendizaje efectivo y significativo.
      .col-lg-auto.d-none.d-lg-block(data-aos="fade-left")
