          p El dibujo tcnico es un lenguaje universal que trasciende las barreras del idioma y conecta a profesionales de diversas disciplinas en la comunicacin de ideas, diseos y proyectos. Es un medio para plasmar ideas en el papel, una herramienta fundamental en la toma de decisiones y la resolucin de problemas en campos tan diversos como la ingeniera, la arquitectura, la fabricacin, la construccin la agricultura en muchas otras labores. Este apartado pretende dar una base en los principios esenciales del dibujo tcnico, que permitirn a los aprendices traducir conceptos abstractos en representaciones visuales claras y precisas. desde la creacin de dibujos bidimensionales simples hasta la comprensin de planos tridimensionales complejos conceptos bsicos, como las proyecciones ortogonales, la simetra, la escala y la representacin de objetos en diferentes vistas. Adems, se explorar en el uso de herramientas y software de dibujo asistido por computadora (CAD) que han revolucionado la forma en que se crea y se comparte el dibujo tcnico en la era digital. 
          a.anexo.mt-4.mb-lg-0.ms-0.bg-white(href="https://www.youtube.com/watch?v=YS5cHpWLNEE" target="_blank" style="max-width:fit-content;")
