      .titulo-segundo(data-aos="flip-up")
        h2#t_3_1 3.1	 Dibujo artstico Vs. Dibujo tcnico
      
      p(data-aos="fade-right") Es fundamental entender las diferencias entre el #[strong dibujo artstico] y el #[strong dibujo tcnico], ya que ambos son formas de expresin grfica, pero con objetivos y enfoques distintos.

      .row.mt-4.justify-content-center
        .col-lg.mb-4.mb-lg-0.col-md-9(data-aos="zoom-in")
          .bg-white.h-100(style="box-shadow:0px 4px 20px rgba(0,0,0,.1);border-radius:14px;")
            figure
              img(src="@/assets/curso/tema3/3.png")
            .p-4
              h5 Dibujo artstico
              p Se utiliza como medio de expresin personal y creativa. Su enfoque principal es esttico, y suele estar cargado de subjetividad, pues depende de la percepcin y estilo del artista. No sigue normas estrictas; el artista tiene libertad para experimentar con formas, proporciones, texturas y colores. No busca precisin, sino transmitir emociones, ideas o conceptos abstractos.
              p #[strong Ejemplos de aplicacin:] artes plsticas, diseo grfico, ilustracin, y artes visuales en general.
        .col-lg.col-md-9(data-aos="zoom-in")
          .bg-white.h-100(style="box-shadow:0px 4px 20px rgba(0,0,0,.1);border-radius:14px;")
            figure
              img(src="@/assets/curso/tema3/4.png")
            .p-4
              h5 Dibujo tcnico
              p Tiene el objetivo de comunicar informacin precisa sobre un objeto o sistema. Su funcin es facilitar la comprensin de las caractersticas fsicas y dimensionales de un objeto para su fabricacin, ensamblaje o mantenimiento. As mismo, Sigue normas y estndares que aseguran uniformidad y claridad. Se enfoca en la precisin, utilizando proyecciones, escalas y acotaciones para representar el objeto de manera objetiva.
              p #[strong Ejemplos de aplicacin:] planos de ingeniera, diseo mecnico, arquitectura, sistemas elctricos, y planos industriales.
      
      .cont_decorativo.mb-4.mt-5(data-aos="fade-right")
        .row
          .col-auto.pe-0
