              img(src="@/assets/curso/tema1/3.svg")
          .col.ps-2(style="place-self:center;")
            h3.mb-0.pe-4 Importancia del dibujo tcnico en la industria
      
      p(data-aos="fade-right") El dibujo tcnico es esencial en mltiples campos de la industria, como la ingeniera mecnica, la arquitectura, la construccin, y la manufactura. Su importancia radica en que acta como un lenguaje universal, que permite a profesionales de diferentes reas entender, visualizar y construir los mismos objetos o estructuras sin margen de error.

      .row.mt-4
        .col-lg-auto.d-none.d-lg-block(data-aos="fade-right")
          figure
            img(src="@/assets/curso/tema3/5.png")
        .col-lg(data-aos="fade-left")
          AcordionRED(tipo="a" clase-tarjeta="tarjeta")
            .row.col-xl-11.m-auto(titulo="Comunicacin Eficiente")
              p Los planos y dibujos tcnicos son un medio de comunicacin precisa entre diseadores, ingenieros, operarios, y tcnicos. Permiten que todos comprendan las especificaciones de un proyecto de manera uniforme, asegurando que el producto final cumpla con los requisitos deseados.
            .row.col-xl-11.m-auto(titulo="Eliminacin de Ambigedades")
              p Al estar normalizado, el dibujo tcnico minimiza las interpretaciones subjetivas. Los detalles como dimensiones, materiales y tolerancias se representan con exactitud, lo cual reduce el riesgo de errores en la fabricacin o construccin.
            .row.col-xl-11.m-auto(titulo="Optimizacin de Procesos")
              p En el mbito de la manufactura, el dibujo tcnico facilita el proceso de produccin, ya que proporciona toda la informacin necesaria para ensamblar, mecanizar, o construir cada pieza. Esto aumenta la eficiencia y permite que los productos sean fabricados a escala con un alto nivel de precisin.
            .row.col-xl-11.m-auto(titulo="Aseguramiento de Calidad")
              p Al definir claramente las especificaciones de diseo, el dibujo tcnico permite verificar que los productos cumplen con los estndares de calidad requeridos. Los tcnicos pueden realizar controles de calidad basados en las dimensiones y tolerancias indicadas en los planos.

    p.mt-5(data-aos="fade-right") En el contexto de un proyecto, el dibujo tcnico se convierte en un documento de referencia que gua todo el proceso, desde la fase de diseo hasta la produccin y el mantenimiento.

    .cont_decorativo.mb-4.mt-5(data-aos="fade-right")
      .row
        .col-auto.pe-0
          figure
            img(src="@/assets/curso/tema1/3.svg")
        .col.ps-2(style="place-self:center;")
          h3.mb-0.pe-4 Aplicacin del dibujo tcnico en procesos industriales
    
    p(data-aos="fade-right") En la industria, el dibujo tcnico no solo describe las caractersticas fsicas de un objeto, sino que tambin se integra en los procesos de produccin y ensamblaje. A continuacin, se describen algunas de sus aplicaciones en procesos industriales:

    .row.mt-4
      .col-lg(data-aos="fade-right")
        AcordionRED(tipo="a" clase-tarjeta="tarjeta")
          .row.col-xl-11.m-auto(titulo="Diseo de componentes y productos")
            p los ingenieros y diseadores utilizan el dibujo tcnico para detallar cada componente de un producto, especificando materiales, dimensiones y acabados. Esto permite que cada pieza encaje perfectamente en el ensamblaje final.
          .row.col-xl-11.m-auto(titulo="Planificacin de fabricacin")
            p Los dibujos tcnicos son esenciales en la planificacin de la fabricacin, ya que contienen toda la informacin que los operadores necesitan para realizar cortes, perforaciones y ensamblajes precisos. Los planos permiten que cada etapa del proceso de produccin se realice de acuerdo con las especificaciones de diseo.
          .row.col-xl-11.m-auto(titulo="Mantenimiento y reparacin")
            p Los dibujos tcnicos tambin son esenciales para el mantenimiento y reparacin de equipos e infraestructuras. Un plano detallado permite que los tcnicos identifiquen piezas especficas y realicen reparaciones de manera eficaz.
          .row.col-xl-11.m-auto(titulo="Control de calidad y normatividad")
            p Los planos tcnicos definen estndares y tolerancias, lo cual es fundamental para el control de calidad. En muchas industrias, los dibujos tcnicos deben cumplir con normas internacionales (como ISO, ASME) para asegurar que los productos tengan una calidad uniforme y cumplan con los requisitos legales y de seguridad.
