        p.mb-0 El dibujo tcnico requiere precisin y exactitud, lo cual se logra con el uso adecuado de instrumentos especficos. Esta seccin detalla los principales instrumentos empleados en el dibujo tcnico, la correcta ejecucin de letras tcnicas para una comunicacin clara, y los tipos de lneas, esenciales para representar diferentes partes y aspectos de un objeto en un plano.
    
    .cont_decorativo.mb-4.mt-5(data-aos="fade-right")
