          h3.mb-0.pe-4 Instrumentos bsicos del dibujo tcnico
    
    p(data-aos="fade-right") Cada instrumento de dibujo tcnico tiene una funcin especfica y su uso correcto es clave para obtener un trazado limpio y preciso. A continuacin, se describen los principales instrumentos.

    .col-xl-8.m-auto.mt-5
      .titulo-sexto.color-acento-contenido.mt-5(data-aos="fade-down")
        h5 Tabla 4.
        span Instrumentos bsicos de dibujo
      .tabla-a.color-acento-botones(data-aos="fade-up")
        table
          caption.fw-normal Fuente: OIT, 2024.
          thead(style="border:0;")
            tr.text-center(style="background-color:#78B62E;")
              th Instrumento
              th Observacin
          tbody
            tr(style="background-color:#F6F6F6;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;")  Regla
              td(style="border:0;border-right:1px #AFAFAF solid;")  Se emplea para medir distancias y trazar lneas rectas. Es especialmente til para acotar dimensiones en un dibujo.
            tr(style="background-color:#fff;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;")  Escuadra
              td(style="border:0;border-right:1px #AFAFAF solid;")  Generalmente de 30-60 o de 45, permiten trazar ngulos rectos y otras inclinaciones comunes en los planos tcnico.
            tr(style="background-color:#F6F6F6;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;")  Comps
              td(style="border:0;border-right:1px #AFAFAF solid;")  Permite dibujar crculos y arcos precisos. Tambin se usa para dividir longitudes en segmentos iguales.
            tr(style="background-color:#fff;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;")  Transportador
              td(style="border:0;border-right:1px #AFAFAF solid;")  Se utiliza para medir y trazar ngulos. Esto es fundamental en la representacin de figuras complejas que requieren precisin angular.
            tr(style="background-color:#F6F6F6;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;")  Escalmetro
              td(style="border:0;border-right:1px #AFAFAF solid;")  Es una regla de secciones triangulares con diferentes escalas (1:50, 1:100, etc.) que permite representar objetos a una escala reducida o ampliada.
            tr(style="background-color:#fff;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;")  Lpices y Portaminas
              td(style="border:0;border-right:1px #AFAFAF solid;")  Los lpices y portaminas de diferentes durezas (H, HB, 2B, etc.) Se utilizan para trazar lneas de distinto grosor y tonalidad, necesarios para distinguir diferentes partes del dibujo.
    
    .cont_decorativo.mb-4.mt-5(data-aos="fade-right")
      .row
        .col-auto.pe-0
          figure
            img(src="@/assets/curso/tema1/3.svg")
        .col.ps-2(style="place-self:center;")
          h3.mb-0.pe-4 Letras tcnicas
    
    p(data-aos="fade-right") Son un componente esencial en el dibujo tcnico, ya que permiten anotar dimensiones, especificaciones y notas de forma clara y uniforme. La uniformidad y legibilidad son aspectos clave en las letras tcnicas, ya que cualquier ambigedad en la escritura podra generar errores de interpretacin.

    .row.mt-4
      .col-lg-auto.d-none.d-lg-block(data-aos="fade-right")
        figure
          img(src="@/assets/curso/tema3/8.png")
      .col-lg(data-aos="fade-left")
        AcordionRED(tipo="a" clase-tarjeta="tarjeta")
          .row.col-xl-11.m-auto(titulo="Estilo de letras ")
            p En el dibujo tcnico, se emplean generalmente letras en maysculas, de trazo uniforme y claro. La estandarizacin en el tamao y el estilo facilita la lectura del dibujo por diferentes personas.
          .row.col-xl-11.m-auto(titulo="Altura y espaciado")
            p Las letras deben tener una altura constante, normalmente de 3 a 5 mm, dependiendo de las especificaciones del plano. El espaciado entre letras y palabras debe ser uniforme para evitar confusin.
          .row.col-xl-11.m-auto(titulo="Uso de plantillas o guas")
            p Para asegurar que el trazo de cada letra sea consistente, se recomienda utilizar una plantilla de letras o guas de trazado. Las plantillas permiten realizar letras de tamao y espaciado constantes, lo cual es especialmente til en planos detallados con muchas anotaciones.
    
    .cont_decorativo.mb-4.mt-5(data-aos="fade-right")
      .row
        .col-auto.pe-0
          figure
            img(src="@/assets/curso/tema1/3.svg")
        .col.ps-2(style="place-self:center;")
          h3.mb-0.pe-4 Tipos de lnea y su significado
    
    p(data-aos="fade-right") En el dibujo tcnico, existen diferentes tipos de lnea, cada una con un propsito especfico. Estas lneas permiten representar detalles del objeto y ayudan a evitar confusin en la interpretacin del dibujo. Los principales tipos de lnea son:

    .col-xl-8.m-auto.mt-5
      .titulo-sexto.color-acento-contenido.mt-5(data-aos="fade-down")
        h5 Tabla 5.
        span Tipos de lneas en dibujo tcnico
      .tabla-a.color-acento-botones(data-aos="fade-up")
        table
          caption.fw-normal Fuente: OIT, 2024.
          thead(style="border:0;")
            tr.text-center(style="background-color:#78B62E;")
              th Tipo de lnea
              th Uso/caracterstica
          tbody
            tr(style="background-color:#F6F6F6;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;")  Lnea de contorno
              td(style="border:0;border-right:1px #AFAFAF solid;")  
                p Representa el contorno visible del objeto. Es el borde principal que define la forma y los lmites del objeto en el dibujo. 
                p Se traza con lneas continuas y de grosor medio a grueso para que sean visibles de inmediato.
            tr(style="background-color:#fff;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;") Lnea de centro
              td(style="border:0;border-right:1px #AFAFAF solid;")  
                p Indica el centro de un objeto simtrico, como el centro de un crculo o el eje de una pieza.
                p Se representa con una lnea de trazos largos y cortos alternados. Este tipo de lnea ayuda a identificar la simetra y los puntos centrales en el diseo.
            tr(style="background-color:#F6F6F6;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;")  Lnea oculta
              td(style="border:0;border-right:1px #AFAFAF solid;")  
                p Indica las partes del objeto que estn ocultas detrs de otras partes, es decir, partes no visibles desde la vista actual.
                p Se traza con una lnea de trazos cortos y equidistantes. Las lneas ocultas son tiles para visualizar el objeto completo, incluyendo los elementos que no se ven directamente en la vista principal.
            tr(style="background-color:#fff;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;") Lnea de acotacin
              td(style="border:0;border-right:1px #AFAFAF solid;")  
                p Especifica dimensiones y tolerancias. Se utiliza para indicar medidas exactas del objeto, as como detalles especficos de tamao o posicin.
                p Es una lnea continua y delgada con flechas en ambos extremos, indicando el punto desde donde se toma la medida. Las cifras de acotacin (medidas) se colocan sobre o junto a la lnea para una interpretacin clara.
            tr(style="background-color:#F6F6F6;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;")  Lnea de proyeccin
              td(style="border:0;border-right:1px #AFAFAF solid;")  
                p Gua la ubicacin de las acotaciones y seala los lmites entre los que se mide. Se traza como una lnea delgada y contina desde el punto del objeto hasta la lnea de acotacin
            tr(style="background-color:#fff;")
              td.text-center(style="border:0;border-right:1px #AFAFAF solid;") Lnea de eje
              td(style="border:0;border-right:1px #AFAFAF solid;")  Representa los ejes de simetra o el eje longitudinal de elementos cilndricos o redondos. Es una lnea delgada compuesta por trazos largos separados por un pequeo espacio.

    p.mt-5(data-aos="fade-right") Cada tipo de lnea tiene un propsito especfico en el dibujo tcnico y contribuye a la claridad y precisin de la representacin grfica. Usar correctamente estos tipos de lnea garantiza que el dibujo pueda ser interpretado sin ambigedades, facilitando la comunicacin de informacin tcnica.

    .col-xl-10.m-auto.mt-5
      .titulo-sexto.color-acento-contenido.mt-5(data-aos="fade-down")
        h5 Figura 2.
        span Tipos de lnea en dibujo tcnico
      .p-3.p-lg-4(style="background-color:#F9F7F4;border-radius:15px;")
        figure(data-aos="fade-up")
          img(src="@/assets/curso/tema3/9.svg" alt="La Figura 2 se denomina Tipos de lnea en dibujo tcnico. La imagen muestra una gua de tipos de lneas utilizadas en dibujo tcnico, incluyendo 12 diferentes estilos desde lneas gruesas continuas hasta lneas finas con zigzag. Cada tipo se acompaa de su descripcin y uso especfico, como contornos visibles, lneas de cota, ejes de simetra y cortes. Al final incluye una nota sobre las proporciones de grosor (gruesa 1, media 0.5, fina 0.2) y un ejemplo de aplicacin en cortes de figuras.")
    
