    .titulo-segundo(data-aos="flip-up")
      h2#t_3_3 3.3	Nociones de geometra plana

    .row
      .col-md-auto.d-none.d-md-block(data-aos="fade-right" style="place-self:center;")
        figure
          img(src="@/assets/curso/tema3/10.svg")
      .col-md(data-aos="fade-left" style="place-self:center;")
        p.mb-0 La geometra plana es la base del dibujo tcnico, ya que proporciona los elementos fundamentales para construir representaciones grficas precisas de objetos y estructuras. La comprensin de conceptos bsicos como puntos, lneas, segmentos, y ngulos es esencial para trazar figuras y estructuras en planos, y para garantizar que cada trazo y medida sea exacto.
    
    .cont_decorativo.mb-4.mt-5(data-aos="fade-right")
      .row
        .col-auto.pe-0
          figure
            img(src="@/assets/curso/tema1/3.svg")
        .col.ps-2(style="place-self:center;")
          h3.mb-0.pe-4  Conceptos bsicos
    
    p(data-aos="fade-right") Incluyen los elementos ms simples y esenciales en el dibujo tcnico, que permiten definir y estructurar figuras ms complejas; entre ellos se encuentran:

    .row.mt-4
      .col-lg-auto.d-none.d-lg-block(data-aos="fade-right")
        figure
          img(src="@/assets/curso/tema3/11.png")
      .col-lg(data-aos="fade-left")
        .p-3.p-lg-4(style="background-color:#294B6D;border-radius:5px;")
          ul.lista-ul.mb-0
            li.d-flex.bg-white.p-2(style="border-radius:4px;")
              i.fas.fa-check-circle(style="color:#78B62E;")
              span #[strong Punto:] Es la unidad geomtrica ms bsica y representa una ubicacin exacta en el espacio, sin dimensiones ni extensin. En los planos tcnicos, los puntos se usan para sealar ubicaciones precisas, como centros de crculos, vrtices de figuras geomtricas y puntos de interseccin entre lneas. Son referencias importantes para construir otras figuras.
            li.d-flex.bg-white.p-2(style="border-radius:4px;")
              i.fas.fa-check-circle(style="color:#78B62E;")
              span #[strong Lnea:] es una sucesin infinita de puntos en una sola dimensin. Tiene longitud, pero no ancho ni profundidad. Se utilizan para representar los contornos de objetos, los ejes de simetra y las divisiones entre diferentes partes de un plano. Existen lneas rectas (en una sola direccin) y curvas (cambian de direccin continuamente), y su trazo es esencial en el diseo de perfiles y contornos de piezas.
            li.d-flex.bg-white.p-2.mb-0(style="border-radius:4px;")
              i.fas.fa-check-circle(style="color:#78B62E;")
              span #[strong Segmento:] Es una porcin de lnea delimitada por dos puntos finales. A diferencia de una lnea, un segmento tiene una longitud finita. Se usan para definir bordes y lmites especficos dentro de un diseo, como los lados de un rectngulo o el radio de un crculo. Se aplican en el trazado de perfiles y la construccin de figuras geomtricas.
    
    .row.mt-4
      .col-lg(data-aos="fade-right")
        .p-3.p-lg-4(style="background-color:#294B6D;border-radius:5px;")
          ul.lista-ul.mb-0
            li.d-flex.bg-white.p-2(style="border-radius:4px;")
              i.fas.fa-check-circle(style="color:#78B62E;")
              span #[strong ngulo:] Es la medida de la apertura formada entre dos lneas o segmentos que se intersecan en un punto llamado vrtice.
            li.d-flex.bg-white.p-2(style="border-radius:4px;")
